// @ts-nocheck
import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmTimeProgress,
  VmCurrentTime,
  VmTime,
  VmEndTime 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-time-progress', VmTimeProgress);
define('vm-current-time', VmCurrentTime);
define('vm-time', VmTime);
define('vm-end-time', VmEndTime);
  

export interface TimeProgressProps extends JSX.VmTimeProgress, HTMLAttributes<HTMLVmTimeProgressElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmTimeProgressElement, TimeProgressProps>(
  'vm-time-progress',
  new Set(['separator','alwaysShowHours']),
);
  